body {
  font-family: sans-serif;
  font-size: 20px;
}

img {
  max-width: 100%;
  height: 500px;
  object-fit: cover;
  display: block;
}

.cat-info {
  gap: 20px;
  display: flex;
}

.description {
  max-width: 50%;
}

.error {
  color: red;
}

.is-hidden {
  display: none;
}

.breed-select {
  margin-bottom: 15px;
  padding: 10px;
  font-size: 17px;
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  animation: spin 2s linear infinite;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

